import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivationEnd, Router } from '@angular/router';

import { UtilsProvider } from '../../util/utils-provider';
import { MenuNivelEnum } from '../../enum/menuNivelEnum';
import { ItemMenuNormalComponent } from './item-menu-normal/item-menu-normal.component';

export declare type MatDrawerMode = 'over' | 'push' | 'side';

@Component({
  selector: 'dsc-menu-lateral',
  templateUrl: './dsc-menu-lateral.component.html',
  styleUrls: ['./dsc-menu-lateral.component.scss']
})
export class DscMenuLateralComponent implements OnInit, OnChanges {
  @Input()
  dscMenuMode: MatDrawerMode = 'side';

  @Input()
  dscMenuOpen?: boolean;

  dscMenuOpen_: boolean = false;

  @Input()
  dscItemsMenu!: Array<ItemMenu>;
  dscItemsMenu_: Array<ItemMenu> = [];

  @Input()
  dscFormControlInputPesquisa?: FormControl;
  dscFormControlInputPesquisa_?: FormControl;

  @Input()
  dscLogoProduto!: string;
  dscLogoProduto_: string = '';

  @Input()
  dscFixed?: boolean;
  dscFixed_: boolean = false;

  @Input()
  dscTopGap?: number;
  dscTopGap_?: number;

  @Input()
  dscBottomGap?: number;
  dscBottomGap_?: number;

  @Input()
  dscMarginTopSidenavContent: number = 0;

  menuNivelEnum = MenuNivelEnum;

  mapaNivelEnum: Map<number, MenuNivelEnum> = new Map<number, MenuNivelEnum>();

  refAtualAtiva?: ItemMenuNormalComponent;

  rotaAtual!: string;

  constructor(private router: Router) {
    this.popularMapaNivelEnum();
  }

  ngOnInit(): void {
    this.escutarRotas();
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscMenuOpen, value => {
      this.dscMenuOpen_ = value;
    });

    UtilsProvider.ifChanged(changes.dscFormControlInputPesquisa, value => {
      this.dscFormControlInputPesquisa_ = value;
    });

    UtilsProvider.ifChanged(changes.dscItemsMenu, value => {
      this.dscItemsMenu_ = value;
    });

    UtilsProvider.ifChanged(changes.dscLogoProduto, value => {
      this.dscLogoProduto_ = value;
    });

    UtilsProvider.ifChanged(changes.dscFixed, value => {
      this.dscFixed_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTopGap, value => {
      this.dscTopGap_ = value;
    });

    UtilsProvider.ifChanged(changes.dscBottomGap, value => {
      this.dscBottomGap_ = value;
    });
  }

  isNormal(itemMenu: ItemMenu): boolean {
    return !itemMenu.filhos || itemMenu.filhos.length == 0;
  }

  isExpansivel(itemMenu: ItemMenu): boolean {
    return !!itemMenu.filhos && itemMenu.filhos.length > 0;
  }

  private popularMapaNivelEnum() {
    this.mapaNivelEnum.set(1, MenuNivelEnum.PRIMEIRO);
    this.mapaNivelEnum.set(2, MenuNivelEnum.SEGUNDO);
    this.mapaNivelEnum.set(3, MenuNivelEnum.TERCEIRO);
  }

  ouvirCallBack(itemMenu: any) {
    if (!!itemMenu.callBack) {
      itemMenu.callBack.call();
    }
  }

  tratarCallBackSelecionado(refNormal: ItemMenuNormalComponent) {
    if (!!this.refAtualAtiva) {
      const anterior = this.refAtualAtiva;
      anterior.isAtivo = false;
    }
    this.refAtualAtiva = refNormal;
    this.refAtualAtiva.isAtivo = true;
  }

  private escutarRotas() {
    this.rotaAtual = this.router.url;
    this.router.events.subscribe(event => {
      if (event instanceof ActivationEnd) {
        this.rotaAtual = this.router.url;
      }
    });
  }
}

export interface ItemMenu {
  nome: string;
  icone?: string;
  url?: string;
  callBack?: Function;
  filhos?: Array<ItemMenu>;
}
