import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';

import { ToolbarTypeConfig } from './shared/toolbar-type-config';
import { UtilsProvider } from '../../util/utils-provider';

@Component({
  selector: 'dsc-header',
  templateUrl: './dsc-header.component.html',
  styleUrls: ['./dsc-header.component.scss']
})
export class DscHeaderComponent implements AfterViewInit, OnDestroy {
  readonly toolbarTypes = new Map<string, ToolbarTypeConfig>([
    ['type_1', { isLogoFull: true, isToogleButton: true, isToogleButtonRight: false, isToggleButtonLabel: true }],
    ['type_2', { isLogoFull: true, isToogleButton: true, isToogleButtonRight: true, isToggleButtonLabel: true }],
    ['type_3', { isLogoFull: true, isToogleButton: false, isToogleButtonRight: false, isToggleButtonLabel: false }],
    ['type_4', { isLogoFull: true, isToogleButton: true, isToogleButtonRight: false, isToggleButtonLabel: false }],
    ['type_5', { isLogoFull: true, isToogleButton: true, isToogleButtonRight: true, isToggleButtonLabel: false }],
    ['type_6', { isLogoFull: false, isToogleButton: true, isToogleButtonRight: false, isToggleButtonLabel: true }],
    ['type_7', { isLogoFull: false, isToogleButton: true, isToogleButtonRight: true, isToggleButtonLabel: true }],
    ['type_8', { isLogoFull: false, isToogleButton: false, isToogleButtonRight: false, isToggleButtonLabel: false }],
    ['type_9', { isLogoFull: false, isToogleButton: true, isToogleButtonRight: false, isToggleButtonLabel: false }],
    ['type_10', { isLogoFull: false, isToogleButton: true, isToogleButtonRight: true, isToggleButtonLabel: false }]
  ]);

  private _mobileQueryListener: () => void;

  @ViewChild('toolbarDiv', { read: ElementRef }) toolbarDiv!: ElementRef;

  @Input()
  dscTipoAcesso: 'interno' | 'externo' = 'interno';

  @Input()
  dscSiglaSistema: string = 'Sigla';

  @Input()
  dscNomeSistema: string = 'Nome do sistema';

  @Input()
  dscTipoClassificacaoConteudo: 'publico' | 'confidencial' = 'publico';

  @Input()
  dscClassificacaoConteudo?: string;

  @Input()
  dscTipoVariacaoHeader: string = 'type_1';

  mobileQuery: MediaQueryList;

  @Output() toggleEvent: EventEmitter<void> = new EventEmitter();

  @Output() trademarkEvent: EventEmitter<void> = new EventEmitter();

  @Output() toolbarHeightEvent: EventEmitter<void> = new EventEmitter();

  isLogoFull: boolean = true;

  isToogleButton: boolean = true;

  isToogleButtonRight: boolean = false;

  isToggleButtonLabel: boolean = true;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher) {
    this.mobileQuery = media.matchMedia('(max-width: 599px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addEventListener('change', this._mobileQueryListener);
  }

  ngAfterViewInit(): void {
    const divHeight = this.toolbarDiv.nativeElement.offsetHeight;
    this.toolbarHeightEvent.emit(divHeight);
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscTipoVariacaoHeader, value => {
      this._buildToolbarBy(value);
    });
  }
  private _buildToolbarBy(toolbarType: string) {
    let toolbarTypeConfig: ToolbarTypeConfig | undefined = this.toolbarTypes.get(toolbarType);
    if (toolbarTypeConfig === undefined) {
      toolbarTypeConfig = {
        isLogoFull: true,
        isToogleButton: true,
        isToogleButtonRight: false,
        isToggleButtonLabel: true
      };
    }
    this.isLogoFull = toolbarTypeConfig.isLogoFull;
    this.isToogleButton = toolbarTypeConfig.isToogleButton;
    this.isToogleButtonRight = toolbarTypeConfig.isToogleButtonRight;
    this.isToggleButtonLabel = toolbarTypeConfig.isToggleButtonLabel;
  }

  onToggle(): void {
    this.toggleEvent.emit();
  }

  onTrandmarkClicked(): void {
    this.trademarkEvent.emit();
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeEventListener('change', this._mobileQueryListener);
  }
}
