import {
  AfterViewInit,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALIDATORS, NG_VALUE_ACCESSOR } from '@angular/forms';
import { DateAdapter, MAT_DATE_LOCALE } from '@angular/material/core';

import { Subscription } from 'rxjs';

// @ts-ignore
import { default as Inputmask } from 'inputmask/dist/inputmask.js';

import { UtilsProvider } from '../../util/utils-provider';
import { TamanhoDatePickerEnum } from '../../enum/datePickerEnums';
import { tamanhoDatePickerType } from '../../types/datePickerTypes';
import { DscDatepickerPtbrAdapter } from './dsc-datepicker-ptbr.adapter';

@Component({
  selector: 'dsc-date-picker',
  templateUrl: './dsc-date-picker.component.html',
  styleUrls: ['./dsc-date-picker.component.scss'],
  providers: [
    { provide: MAT_DATE_LOCALE, useValue: 'pt-BR' },
    { provide: DateAdapter, useClass: DscDatepickerPtbrAdapter },
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: DscDatePickerComponent
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: DscDatePickerComponent
    }
  ]
})
export class DscDatePickerComponent implements OnInit, OnChanges, AfterViewInit, ControlValueAccessor {
  @Input()
  dscPlaceHolder?: string;
  dscPlaceHolder_: string = '';

  @Input()
  dscRotulo?: string;
  dscRotulo_: string = '';

  @Input()
  dscInformacaoAdicional?: string;
  dscInformacaoAdicional_?: string;

  @Input()
  dscIconeInformativo?: string;
  dscIconeInformativo_?: string;

  @Input()
  dscTipoTamanhoDatePicker: tamanhoDatePickerType = TamanhoDatePickerEnum.STANDARD;
  dscTipoTamanhoDatePicker_: TamanhoDatePickerEnum = TamanhoDatePickerEnum.STANDARD;

  mapaTamanhoDatePicker: Map<TamanhoDatePickerEnum, string> = new Map<TamanhoDatePickerEnum, string>();

  mapaHeader: Map<TamanhoDatePickerEnum, string> = new Map<TamanhoDatePickerEnum, string>();

  classeHeader: string = 'header-standard';

  classFormField = '';

  onTouched: Function = () => {};

  onChangeSubs: Subscription[] = [];

  formControlDatePicker = new FormControl('');

  @ViewChild('dateInput', { static: true, read: ElementRef })
  input!: ElementRef;

  constructor() {
    this.inicializarMapaTamanhoInput();
    this.inicializarMapaHeader();
  }

  ngOnInit(): void {
    this.aplicarClasseMatFormField();
  }

  ngAfterViewInit(): void {
    this._configurarInputMask();
  }

  private _configurarInputMask(): void {
    Inputmask({
      alias: 'datetime',
      inputFormat: 'dd/mm/yyyy',
      placeholder: 'dd/mm/aaaa',
      showMaskOnHover: false,
      clearIncomplete: true
    }).mask(this.input.nativeElement);
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscPlaceHolder, value => {
      this.dscPlaceHolder_ = value;
    });

    UtilsProvider.ifChanged(changes.dscRotulo, value => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, value => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, value => {
      this.dscIconeInformativo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTipoTamanhoDatePicker, value => {
      this.dscTipoTamanhoDatePicker_ = value;
      this.aplicarClasseMatFormField();
    });
  }

  ngOnDestroy() {
    for (let sub of this.onChangeSubs) {
      sub.unsubscribe();
    }
  }

  validate() {
    if (this.formControlDatePicker.valid) {
      return null;
    }
    let errors: any = this.formControlDatePicker.errors || {};

    return errors;
  }

  registerOnChange(onChange: any) {
    const sub = this.formControlDatePicker.valueChanges.subscribe(onChange);
    this.onChangeSubs.push(sub);
  }

  setDisabledState(disabled: boolean) {
    if (disabled) {
      this.formControlDatePicker.disable();
    } else {
      this.formControlDatePicker.enable();
    }
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.formControlDatePicker.setValue(value, { emitEvent: false });
    }
  }

  registerOnTouched(onTouched: Function) {
    this.onTouched = onTouched;
  }

  inicializarMapaTamanhoInput() {
    this.mapaTamanhoDatePicker.set(TamanhoDatePickerEnum.LARGE, 'dsc-large');
    this.mapaTamanhoDatePicker.set(TamanhoDatePickerEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoDatePicker.set(TamanhoDatePickerEnum.SMALL, 'dsc-small');
  }

  inicializarMapaHeader() {
    this.mapaHeader.set(TamanhoDatePickerEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoDatePickerEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoDatePickerEnum.SMALL, 'header-small');
  }

  aplicarClasseMatFormField() {
    this.classFormField = '';
    this.classFormField = this.mapaTamanhoDatePicker.get(this.dscTipoTamanhoDatePicker_) || '';
    this.classeHeader = this.mapaHeader.get(this.dscTipoTamanhoDatePicker_) || '';
  }
}
