import { Component, EventEmitter, Input, Output, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { UtilsProvider } from '../../util/utils-provider';
import { direcaoIconeType, tamanhoBotaoType, tipoBotaoType } from '../../types/buttonTypes';
import { TamanhoBotaoEnum, TipoBotaoEnum, DirecaoIconeEnum } from '../../enum/buttonEnums';

@Component({
  selector: 'dsc-button',
  templateUrl: './dsc-button.component.html',
  styleUrls: ['./dsc-button.component.scss']
})
export class DscButtonComponent implements OnInit, OnChanges {

  @Input()
  dscTextoBotao!: string;

  dscTextoBotao_: string = ''

  @Input()
  dscTamanhoBotao: tamanhoBotaoType = TamanhoBotaoEnum.STANDARD;

  dscTamanhoBotao_: TamanhoBotaoEnum = TamanhoBotaoEnum.STANDARD;

  @Input()
  dscTipoBotao: tipoBotaoType = TipoBotaoEnum.PRIMARY;

  dscTipoBotao_: TipoBotaoEnum = TipoBotaoEnum.PRIMARY;

  @Input()
  dscDesabilitado: boolean = false;
  dscDesabilitado_: boolean = false;

  @Input()
  dscIcone?: string;

  dscIcone_: string = '';

  @Input()
  dscDirecaoIcone: direcaoIconeType = DirecaoIconeEnum.LEFT;

  dscDirecaoIcone_: DirecaoIconeEnum = DirecaoIconeEnum.LEFT;

  @Output()
  dscClick: EventEmitter<void> = new EventEmitter<void>();

  estilo_botao: string = '';

  estilo_texto_botao: string = 'texto';

  mapaTamanhoBotao: Map<TamanhoBotaoEnum, string> = new Map<TamanhoBotaoEnum, string>();

  mapaTipoBotao: Map<TipoBotaoEnum, string> = new Map<TipoBotaoEnum, string>();



  constructor() {
    this.preencherMapaTamanhoBotao();
    this.preencherMapaTipoBotao();
  }

  ngOnInit(): void {
    this.carregarClasses();
  }

  ngOnChanges(changes: SimpleChanges): void {

    UtilsProvider.ifChanged(changes.dscTextoBotao, (value) => {
      this.dscTextoBotao_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTamanhoBotao, (value) => {
      this.dscTamanhoBotao_ = value;
      this.estilo_botao = '';
      this.carregarClasses();
    });

    UtilsProvider.ifChanged(changes.dscTipoBotao, (value) => {
      this.dscTipoBotao_ = value;
      this.estilo_botao = '';
      this.carregarClasses();
    });

    UtilsProvider.ifChanged(changes.dscDesabilitado, (value) => {
      this.dscDesabilitado_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIcone, (value) => {
      this.dscIcone_ = value;
      this.tratarTextoBotao();
    });

    UtilsProvider.ifChanged(changes.dscDirecaoIcone, (value) => {
      this.dscDirecaoIcone_ = value;
      this.tratarTextoBotao();
    });

  }

  private preencherMapaTamanhoBotao() {
    this.mapaTamanhoBotao.set(TamanhoBotaoEnum.LARGE, 'dsc-large');
    this.mapaTamanhoBotao.set(TamanhoBotaoEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoBotao.set(TamanhoBotaoEnum.SMALL, 'dsc-small');
  }

  private preencherMapaTipoBotao() {
      this.mapaTipoBotao.set(TipoBotaoEnum.PRIMARY, 'dsc-primary');
      this.mapaTipoBotao.set(TipoBotaoEnum.SECONDARY, 'dsc-secondary');
      this.mapaTipoBotao.set(TipoBotaoEnum.DANGER, 'dsc-danger');
      this.mapaTipoBotao.set(TipoBotaoEnum.AUX, 'dsc-aux');
      this.mapaTipoBotao.set(TipoBotaoEnum.AUX_DEST, 'dsc-aux-dest');
  }

  public carregarClasses() {
    this.pegarClassePorChave(this.mapaTamanhoBotao, this.dscTamanhoBotao_);
    this.pegarClassePorChave(this.mapaTipoBotao, this.dscTipoBotao_);
  }

  private pegarClassePorChave(mapa: Map<any, any>, chave: any) {
    const nomeClasse: string = mapa.get(chave) || '';
    if (nomeClasse.length > 0) {
      this.aplicarClasseDinamica(nomeClasse);
    } else {
      console.error('O valor '+ chave +  ' está inválido');
    }
  }

  private aplicarClasseDinamica(nomeClasse: string) {
    if (this.estilo_botao.length > 0) {
      this.estilo_botao = this.estilo_botao.concat(" " + nomeClasse);
    } else {
      this.estilo_botao = this.estilo_botao.concat(nomeClasse);
    }
  }

  tratarTextoBotao() {
    if (this.isRight())
      this.estilo_texto_botao = "texto-left";
    else if (this.isLeft())
      this.estilo_texto_botao = "texto-right"
    else
      this.estilo_texto_botao = "texto";
  }


  public isLeft(): boolean {
    return this.dscIcone_.length > 0 &&  this.dscDirecaoIcone_ === DirecaoIconeEnum.LEFT;
  }

  public isRight(): boolean {
    return this.dscIcone_.length > 0 && this.dscDirecaoIcone_ === DirecaoIconeEnum.RIGHT;
  }

  tratarClick() {
    this.dscClick.emit();
  }
}
