import {
  Component,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
} from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  Validator,
} from '@angular/forms';
import { tamanhoTextAreaType } from '../../types/textAreaTypes';
import { TamanhoTextAreaEnum } from '../../enum/textAreaEnums';
import { UtilsProvider } from '../../util/utils-provider';
import { Subscription } from 'rxjs';

@Component({
  selector: 'dsc-text-area',
  templateUrl: './dsc-text-area.component.html',
  styleUrls: ['./dsc-text-area.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: DscTextAreaComponent,
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: DscTextAreaComponent,
    },
  ],
})
export class DscTextAreaComponent
  implements OnInit, OnChanges, ControlValueAccessor, OnDestroy, Validator
{
  @Input()
  dscRotulo: string = '';
  dscRotulo_: string = '';

  @Input()
  dscInformacaoAdicional: string = '';
  dscInformacaoAdicional_: string = '';

  @Input()
  dscPlaceHolder: string = '';
  dscPlaceHolder_: string = '';

  @Input()
  dscTamanhoTextArea: tamanhoTextAreaType = TamanhoTextAreaEnum.STANDARD;
  dscTamanhoTextArea_: TamanhoTextAreaEnum = TamanhoTextAreaEnum.STANDARD;

  @Input()
  dscIconeInformativo?: string;
  dscIconeInformativo_?: string;

  @Input()
  dscTamanhoMaximo: number = 45;

  @Input()
  dscIsContadorCaractere: boolean = false;

  @Input()
  dscTextoComplementar: string = '';

  formControlTextArea = new FormControl('');

  mapaTamanhoTextArea: Map<TamanhoTextAreaEnum, string> = new Map<
    TamanhoTextAreaEnum,
    string
  >();

  mapaHeader: Map<TamanhoTextAreaEnum, string> = new Map<
    TamanhoTextAreaEnum,
    string
  >();

  classeTextArea: string = 'dsc-standard';

  classeHeader: string = 'header-standard';

  classeDisable = '';

  classeBorda = ' empty';

  onTouched: Function = () => {};

  onChangeSubs: Subscription[] = [];

  constructor(private formBuilder: FormBuilder) {
    this.preencherMapaTamanhoTextArea();
    this.preencherMapaHeader();
  }

  ngOnDestroy() {
    for (let sub of this.onChangeSubs) {
      sub.unsubscribe();
    }
  }

  validate(control: AbstractControl) {
    if (this.formControlTextArea.valid) {
      return null;
    }

    let errors: any = this.formControlTextArea.errors || {};

    return errors;
  }

  registerOnChange(onChange: any) {
    const sub = this.formControlTextArea.valueChanges.subscribe(onChange);
    this.onChangeSubs.push(sub);
  }

  setDisabledState(disabled: boolean) {
    if (disabled) {
      this.classeDisable = ' container-disabled';
      this.formControlTextArea.disable();
    } else {
      this.classeDisable = '';
      this.formControlTextArea.enable();
    }
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.formControlTextArea.setValue(value, { emitEvent: false });
    }
  }

  registerOnTouched(onTouched: Function) {
    this.onTouched = onTouched;
  }

  ngOnInit(): void {
    this.escutarInputTratarBorda();
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscRotulo, (value) => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, (value) => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscPlaceHolder, (value) => {
      this.dscPlaceHolder_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTamanhoTextArea, (value) => {
      this.dscTamanhoTextArea_ = value;
      this.tratarClassesDinamicas();
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, (value) => {
      this.dscIconeInformativo_ = value;
    });
  }

  private tratarClassesDinamicas() {
    this.classeTextArea =
      this.mapaTamanhoTextArea.get(this.dscTamanhoTextArea_) || '';
    this.classeHeader = this.mapaHeader.get(this.dscTamanhoTextArea_) || '';
  }

  private preencherMapaTamanhoTextArea() {
    this.mapaTamanhoTextArea.set(TamanhoTextAreaEnum.LARGE, 'dsc-large');
    this.mapaTamanhoTextArea.set(TamanhoTextAreaEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoTextArea.set(TamanhoTextAreaEnum.SMALL, 'dsc-small');
  }

  private preencherMapaHeader() {
    this.mapaHeader.set(TamanhoTextAreaEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoTextAreaEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoTextAreaEnum.SMALL, 'header-small');
  }

  escutarInputTratarBorda() {
    this.formControlTextArea.valueChanges.subscribe((value: string) => {
      this.classeBorda = ' empty';
      if (value.length > 0) {
        this.classeBorda = ' filled';
      }
    });
  }
}
