import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from "@angular/core";
import { MenuNivelEnum } from "../../../enum/menuNivelEnum";
import { UtilsProvider } from "../../../util/utils-provider";
import { MatExpansionPanel } from "@angular/material/expansion";

@Component({
  selector: 'item-menu-expansivel',
  templateUrl: './item-menu-expansivel.component.html',
  styleUrls: ['./item-menu-expansivel.component.scss']
})
export class ItemMenuExpansivelComponent implements OnInit, OnChanges {
  @Input()
  icone: string = '';

  @Input()
  nome: string = '';

  @Input()
  nivelItem: MenuNivelEnum = MenuNivelEnum.PRIMEIRO;

  @Input()
  referenciaSuperior?: ItemMenuExpansivelComponent;

  @Output()
  callBack: EventEmitter<void> = new EventEmitter<void>();

  @ViewChild(MatExpansionPanel, {static: true}) matExpanselPanel!: MatExpansionPanel;

  classePainel: string = 'fechado';


  constructor() {
  }



  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.icone, (value) => {
      this.icone = value;
    });

    UtilsProvider.ifChanged(changes.nome, (value) => {
      this.nome = value;
    });
  }

  ngOnInit(): void {
  }

  definirClassePainel($event: boolean): void {
    this.classePainel =  $event ? 'aberto' : 'fechado';
  }

  tratarCallBack($event: MouseEvent) {
    $event.stopPropagation();
    this.callBack.emit();
  }

  abrirPainel(): void {
    this.matExpanselPanel.open();
    if (!!this.referenciaSuperior) {
      this.referenciaSuperior.abrirPainel();
    }
  }

  fecharPainel(): void {
    this.matExpanselPanel.close();

  }


}
