import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, TemplateRef } from "@angular/core";
import { UtilsProvider } from "../../util/utils-provider";

@Component({
  selector: 'dsc-tab',
  templateUrl: './dsc-tab.component.html',
  styleUrls: ['./dsc-tab.component.scss']
})
export class DscTabComponent implements OnInit, OnChanges {

  @Input()
  dscAbas!: Array<itemAba>;
  dscAbas_ :Array<itemAba> = [];

  @Input()
  dscIndex?:number;
  dscIndex_?:number = 0;

  @Output()
  dscIndexChange: EventEmitter<number> = new EventEmitter<number>();

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscAbas, (value) => {
      this.dscAbas_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIndex, (value) => {
      this.dscIndex_ = value;
    });
  }

  tratarMudancaIndex($event: number) {
    this.dscIndexChange.emit($event);
  }
}

export interface itemAba {
  nome: string,
  icone?: string,
  templateRef: TemplateRef<any>
}
