import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { UtilsProvider } from '../../util/utils-provider';
import { tamanhoInputType } from '../../types/inputTypes';
import { TamanhoInputEnum } from '../../enum/inputEnums';
import { TamanhoBotaoEnum } from '../../enum/buttonEnums';
import { tamanhoBotaoType } from '../../types/buttonTypes';

@Component({
  selector: 'dsc-input-file',
  templateUrl: './dsc-input-file.component.html',
  styleUrls: ['./dsc-input-file.component.scss'],
})
export class DscInputFileComponent implements OnInit, OnChanges {
  @Input()
  dscRotulo?: string;
  dscRotulo_?: string;

  @Input()
  dscInformacaoAdicional?: string;
  dscInformacaoAdicional_?: string;

  @Input()
  dscIconeInformativo?: string;
  dscIconeInformativo_?: string;

  @Input()
  dscTipoTamanhoInput: tamanhoInputType = TamanhoInputEnum.STANDARD;
  dscTipoTamanhoInput_: TamanhoInputEnum = TamanhoInputEnum.STANDARD;

  @Input()
  dscDisabled?: boolean;
  dscDisabled_: boolean = false;

  @Output()
  dscFile: EventEmitter<File> = new EventEmitter<File>();

  @ViewChild('inputFileRef', { static: true })
  inputRef: any;

  tamanhoBotao: tamanhoBotaoType = 'standard';

  fileName: string = '';

  classeHeader: string = 'header-standard';

  mapaHeader: Map<TamanhoInputEnum, string> = new Map<
    TamanhoInputEnum,
    string
  >();

  mapaBotao: Map<TamanhoInputEnum, TamanhoBotaoEnum> = new Map<
    TamanhoInputEnum,
    TamanhoBotaoEnum
  >();

  mapaSize: Map<TamanhoInputEnum, string> = new Map<TamanhoInputEnum, string>();

  size = 'dsc-standard';

  classeBorda = ' empty';

  constructor() {
    this.inicializarMapaHeader();
    this.inicializarMapaBotao();
    this.inicializarMapaSize();
  }

  ngOnInit(): void {
    this.aplicarClasses();
  }

  ngOnChanges(changes: SimpleChanges) {
    UtilsProvider.ifChanged(changes.dscRotulo, (value) => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, (value) => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, (value) => {
      this.dscIconeInformativo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTipoTamanhoInput, (value) => {
      this.dscTipoTamanhoInput_ = value;
      this.aplicarClasses();
    });

    UtilsProvider.ifChanged(changes.dscDisabled, (value) => {
      this.dscDisabled_ = value;
      this.tratarClasseBorda();
    });
  }

  tratarArquivo($event: any) {
    const file = $event.target.files[0];
    this.fileName = file.name;
    this.classeBorda = ' filled';
    this.dscFile.emit(file);
  }

  clickFile() {
    this.inputRef.nativeElement.click();
  }

  private aplicarClasses() {
    this.classeHeader = this.mapaHeader.get(this.dscTipoTamanhoInput_) || '';
    this.tamanhoBotao = this.mapaBotao.get(this.dscTipoTamanhoInput_) || '';
    this.size = this.mapaSize.get(this.dscTipoTamanhoInput_) || '';
  }

  private tratarClasseBorda() {
    this.classeBorda = this.dscDisabled_ ? 'disabled' : 'empty';
  }

  private inicializarMapaHeader() {
    this.mapaHeader.set(TamanhoInputEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoInputEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoInputEnum.SMALL, 'header-small');
  }

  private inicializarMapaBotao() {
    this.mapaBotao.set(TamanhoInputEnum.LARGE, TamanhoBotaoEnum.LARGE);
    this.mapaBotao.set(TamanhoInputEnum.STANDARD, TamanhoBotaoEnum.STANDARD);
    this.mapaBotao.set(TamanhoInputEnum.SMALL, TamanhoBotaoEnum.SMALL);
  }

  private inicializarMapaSize() {
    this.mapaSize.set(TamanhoInputEnum.LARGE, 'dsc-large');
    this.mapaSize.set(TamanhoInputEnum.STANDARD, 'dsc-standard');
    this.mapaSize.set(TamanhoInputEnum.SMALL, 'dsc-small');
  }
}
