import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from "@angular/core";
import { MenuNivelEnum } from "../../../enum/menuNivelEnum";
import { UtilsProvider } from "../../../util/utils-provider";
import { ItemMenuExpansivelComponent } from "../item-menu-expansivel/item-menu-expansivel.component";

@Component({
  selector: 'item-menu-normal',
  templateUrl: './item-menu-normal.component.html',
  styleUrls: ['./item-menu-normal.component.scss']
})
export class ItemMenuNormalComponent implements OnInit, OnChanges {

  @Input()
  icone: string = '';


  @Input()
  nome: string = '';

  @Input()
  nivelItem: MenuNivelEnum = MenuNivelEnum.PRIMEIRO;

  @Input()
  rotaAtual!: string;

  @Input()
  urlPropria!: string;

  @Input()
  referenciaSuperior?: ItemMenuExpansivelComponent;

  @Output()
  callBack: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  callBackItemSelecionado: EventEmitter<void> = new EventEmitter<void>();

  isAtivo: boolean = false;

  constructor() {
  }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.rotaAtual, (value) => {
      this.rotaAtual = value;
      this.tratarRotaAtual();
    });
  }

  tratarCallBack($event: MouseEvent) {
    $event.stopPropagation();
    this.callBack.emit();
    this.callBackItemSelecionado.emit();
  }

  tratarRotaAtual() {
    if (this.rotaAtual === this.urlPropria) {
      this.callBackItemSelecionado.emit();
      //TODO refatorar o setTimeOut
      //Tempo necessário para o matExpansel panel renderizar seu item filho
      setTimeout(() => {
        this.referenciaSuperior?.abrirPainel();
      }, 650)

    }
  }

}
