import {
  Component,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
} from '@angular/core';

import {
  AbstractControl,
  ControlValueAccessor,
  FormControl,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  Validator,
} from '@angular/forms';

import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { TamanhoSelectEnum } from '../../enum/selectEnums';
import { UtilsProvider } from '../../util/utils-provider';
import {
  tamanhoSelectType,
  tipoTextoFeedBackType,
} from '../../types/selectTypes';


@Component({
  selector: 'dsc-select',
  templateUrl: './dsc-select.component.html',
  styleUrls: ['./dsc-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: DscSelectComponent,
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: DscSelectComponent,
    },
  ],
})
export class DscSelectComponent
  implements OnInit, OnChanges, ControlValueAccessor, OnDestroy, Validator
{
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  @Input()
  dscRotulo: string = '';
  dscRotulo_: string = '';

  @Input()
  dscInformacaoAdicional: string = '';
  dscInformacaoAdicional_: string = '';

  @Input()
  dscIconeInformativo: string = '';
  dscIconeInformativo_: string = '';

  @Input()
  dscIconLeft: string = '';
  dscIconLeft_: string = '';

  @Input()
  dscTipoTextoFeedBack: tipoTextoFeedBackType = 'default';
  dscTipoTextoFeedBack_: tipoTextoFeedBackType = 'default';

  @Input()
  dscTextoFeedBack: string = '';
  dscTextoFeedBack_: string = '';

  @Input()
  dscTextoComplementar: string = '';
  dscTextoComplementar_: string = '';

  @Input()
  dscPlaceHolder: string = '';
  dscPlaceHolder_: string = '';

  @Input()
  dscIsMultiple: boolean = false;
  dscIsMultiple_: boolean = false;

  @Input()
  dscOptions: { label: string; value: any }[] = [];
  dscOptions_: { label: string; value: any }[] = [];

  @Input()
  dscOptionsGroup: {
    label: string;
    options: { label: string; value: any }[];
  }[] = [];
  dscOptionsGroup_: {
    label: string;
    options: { label: string; value: any }[];
  }[] = [];

  @Input()
  dscTipoTamanhoSelect: tamanhoSelectType = TamanhoSelectEnum.STANDARD;
  dscTipoTamanhoSelect_: TamanhoSelectEnum = TamanhoSelectEnum.STANDARD;

  @Input()
  dscHasFilter: boolean = false;

  dscHasFilter_: boolean = false;

  optionsAux: { label: string; value: any }[] = [];

  optionsGroupAux: {
    label: string;
    options: { label: string; value: any }[];
  }[] = [];

  icone_feedBack = '';

  classe_box = 'box-input';

  mapaClasseIcone: Map<string, string> = new Map<string, string>();

  formControlSearch: FormControl = new FormControl('');

  classComplementar = 'complementar';

  onTouched: Function = () => {};

  onChangeSubs: Subscription[] = [];

  classFormField = '';

  classeHeader: string = 'header-standard';

  mapaTamanhoSelect: Map<TamanhoSelectEnum, string> = new Map<
    TamanhoSelectEnum,
    string
  >();

  mapaHeader: Map<TamanhoSelectEnum, string> = new Map<
    TamanhoSelectEnum,
    string
  >();

  classeBorda = ' empty';

  formControlSelect = new FormControl([]);

  constructor() {
    this.inicializarMapa();
    this.inicializarMapaHeader();
    this.inicializarMapaTamanhoInput();
  }

  ngOnInit(): void {
    if (!this.dscHasFilter_) this.escutarInputFiltro();
    this.aplicarClasseMatFormField();
    this.escurtarSelectTratarBorda();
  }

  private escutarInputFiltro() {
    this.formControlSearch.valueChanges
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((value) => {
        if (!!this.dscOptions_) {
          if (!!value) {
            this.dscOptions_ = Object.assign(
              [],
              this.optionsAux.filter((e) =>
                e.label.toLowerCase().includes(value.toLowerCase())
              )
            );
          } else {
            this.dscOptions_ = Object.assign([], this.optionsAux);
          }
        }

        if (!!this.dscOptionsGroup_) {
          if (!!value) {
            this.dscOptionsGroup_ = Object.assign(
              [],
              JSON.parse(JSON.stringify(this.optionsGroupAux))
            );

            this.dscOptionsGroup_.forEach((group) => {
              group.options = group.options.filter((e) =>
                e.label.toLowerCase().includes(value.toLowerCase())
              );
            });
          } else {
            this.dscOptionsGroup_ = Object.assign(
              [],
              JSON.parse(JSON.stringify(this.optionsGroupAux))
            );
          }
        }
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscRotulo, (value) => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, (value) => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, (value) => {
      this.dscIconeInformativo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconLeft, (value) => {
      this.dscIconLeft_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTipoTextoFeedBack, (value) => {
      this.dscTipoTextoFeedBack_ = value;
      this.aplicarClasseFeedBack();
      this.aplicarClasseComplementar();
    });

    UtilsProvider.ifChanged(changes.dscTextoFeedBack, (value) => {
      this.dscTextoFeedBack_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTextoComplementar, (value) => {
      this.dscTextoComplementar_ = value;
    });

    UtilsProvider.ifChanged(changes.dscPlaceHolder, (value) => {
      this.dscPlaceHolder_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIsMultiple, (value) => {
      this.dscIsMultiple_ = JSON.parse(value);
    });

    UtilsProvider.ifChanged(changes.dscOptions, (value) => {
      this.dscOptions_ = value;
      this.optionsAux = Object.assign([], this.dscOptions_);
    });

    UtilsProvider.ifChanged(changes.dscOptionsGroup, (value) => {
      this.dscOptionsGroup_ = value;
      this.optionsGroupAux = Object.assign(
        [],
        JSON.parse(JSON.stringify(this.dscOptionsGroup_))
      );
    });

    UtilsProvider.ifChanged(changes.dscTipoTamanhoSelect, (value) => {
      this.dscTipoTamanhoSelect_ = value;
      this.aplicarClasseMatFormField();
    });

    UtilsProvider.ifChanged(changes.dscTipoTamanhoSelect, (value) => {
      this.dscHasFilter_ =  JSON.parse(value);
      this.dscHasFilter_ ? this.escutarInputFiltro() : this.unsubscribeAll();
    });
  }

  validate(control: AbstractControl) {
    if (this.formControlSelect.valid) {
      return null;
    }

    let errors: any = this.formControlSelect.errors || {};

    return errors;
  }

  registerOnChange(onChange: any) {
    const sub = this.formControlSelect.valueChanges.subscribe(onChange);
    this.onChangeSubs.push(sub);
  }

  setDisabledState(disabled: boolean) {
    if (disabled) {
      this.formControlSelect.disable();
    } else {
      this.formControlSelect.enable();
    }
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.formControlSelect.setValue(value, { emitEvent: false });
    }
  }

  registerOnTouched(onTouched: Function) {
    this.onTouched = onTouched;
  }

  aplicarClasseFeedBack() {
    this.classe_box = 'box-input';
    if (this.dscTipoTextoFeedBack_ !== 'default') {
      this.classe_box = this.classe_box + ' ' + this.dscTipoTextoFeedBack_;
      this.icone_feedBack =
        this.mapaClasseIcone.get(this.dscTipoTextoFeedBack_) || '';
    }
  }

  inicializarMapa() {
    this.mapaClasseIcone.set('danger', 'warning');
  }

  inicializarMapaHeader() {
    this.mapaHeader.set(TamanhoSelectEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoSelectEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoSelectEnum.SMALL, 'header-small');
  }

  inicializarMapaTamanhoInput() {
    this.mapaTamanhoSelect.set(TamanhoSelectEnum.LARGE, 'dsc-large');
    this.mapaTamanhoSelect.set(TamanhoSelectEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoSelect.set(TamanhoSelectEnum.SMALL, 'dsc-small');
  }

  aplicarClasseComplementar() {
    this.classComplementar = 'complementar';
    if (this.dscTipoTextoFeedBack_ == 'warning') {
      this.classComplementar = this.classComplementar + ' icon-black';
    } else {
      this.classComplementar = this.classComplementar + ' icon-white';
    }
  }

  tratarAbertura($event: boolean) {
    if (!$event) {
      this.formControlSearch.setValue('');
    }
  }

  aplicarClasseMatFormField() {
    this.classFormField = '';
    this.classFormField =
      this.mapaTamanhoSelect.get(this.dscTipoTamanhoSelect_) || '';
    this.classeHeader = this.mapaHeader.get(this.dscTipoTamanhoSelect_) || '';
  }

  escurtarSelectTratarBorda() {
    this.formControlSelect.valueChanges.subscribe((value: string) => {
      this.classeBorda = ' empty';
      if (value.length > 0) {
        this.classeBorda = ' filled';
      }
    });
  }

  private unsubscribeAll(): void {
    this._unsubscribeAll.next();
    this._unsubscribeAll.complete();
  }

  ngOnDestroy() {
    for (let sub of this.onChangeSubs) {
      sub.unsubscribe();
    }

    this.unsubscribeAll();
  }
}
